body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header {
  overflow: auto; }
  header h1 {
    float: left;
    margin-left: 8px; }
  header nav {
    float: right;
    margin-right: 8px;
    font-size: 1.2em;
    margin-top: 33px; }
    @media (min-width: 769px) {
      header nav {
        font-size: 1.6em;
        margin-top: 27px; } }
    header nav ul {
      margin: 0; }
    header nav li {
      display: inline-block;
      margin-left: 24px; }

.playground textarea {
  width: calc(100% - 48px);
  background: #3a3a3a;
  padding: 24px;
  border-radius: 4px;
  margin-bottom: 12px;
  outline: none;
  resize: vertical;
  border: 0 black;
  color: white;
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
  font-size: 1em;
  line-height: 1.6em; }
  @media (min-width: 769px) {
    .playground textarea {
      margin-bottom: 24px; } }
  .playground textarea:focus {
    box-shadow: 0 0 3px 2px #00b6ff; }

.playground label {
  margin-right: 24px;
  padding-left: 4px; }

.playground button {
  float: right;
  border-radius: 100px;
  padding: 11px 32px 14px; }
  .playground button:before {
    content: "\25B6";
    padding-right: 12px;
    top: 1px;
    position: relative; }

.playground .wrap-content {
  margin: 0 8px; }

.playground .wrap-input {
  overflow: auto; }

@media (min-width: 769px) {
  .playground .wrap-radio {
    float: left; } }

.playground .wrap-radio > div {
  margin-bottom: 8px; }
  @media (min-width: 769px) {
    .playground .wrap-radio > div {
      display: inline-block; } }

@media (min-width: 769px) {
  .playground .wrap-select {
    margin-left: 80px;
    float: left; } }

.playground div.result {
  margin-bottom: 128px; }
  .playground div.result p.output {
    font-style: italic;
    color: #989898;
    line-height: 1.4em; }
  .playground div.result code {
    white-space: pre-wrap; }


a {
  color: #00b6ff;
  text-decoration: none; }

button {
  background-color: #406a7b;
  border-radius: 4px;
  color: white;
  padding: 12px 32px;
  font-size: 1.6em; }
  button:hover {
    background-color: #477688; }

.wrap {
  padding: 0 4px;
  max-width: 1280px;
  margin: 0 auto;
  color: #406a7b; }
  @media (min-width: 769px) {
    .wrap {
      padding: 0 24px; } }

